import React from 'react';
import s from './MyPosts.module.scss';
import Post from './Post';
import { reset, Field, reduxForm} from "redux-form";
import { requiredField, minLength3, maxLength50 } from '../../../utils/validators/validators';
import {Input, Textarea} from "../../common/Forms/Controls";

export default props => {
    const {posts, addPost} = props;
    // console.log('MyPosts', props);

    return (
        <div key={'MyPosts'}>
            <h3>Add new post</h3>
            <div className={s.new}>
                <MyPostFormRedux onSubmit={ data => addPost(data) }/>
            </div>

            <h3>My Posts</h3>
            <div className={s.posts}>
                {posts.map( item => Post(item) )}
            </div>

        </div>
    );
}

const MyPostForm = (props) => {
    const {handleSubmit, invalid} = props;

    return (
        <form onSubmit={handleSubmit} className={'Form'}>
            <Field
                label={'Заголовок'}
                component={Input}
                name={'title'}
                validate={[requiredField, maxLength50, minLength3]}
            />
            <Field
                component={Textarea}
                name={'content'}
                validate={[requiredField, minLength3]}
                label={'Текст поста'}
            />
            <div className={'Form_Row Form_Row__TextRight'}>
                <button type={'submit'} className={'Form_Button'} disabled={invalid}>Отправить</button>
            </div>
        </form>
    )
};

const MyPostFormRedux = reduxForm({
    form: 'new-post-form',
    onSubmitSuccess: (result, dispatch) => dispatch(reset('new-post-form'))
})(MyPostForm);
