import { createSelector } from 'reselect';

export const getProfile = state => {
    return state.profilePageData.profile
};
export const getProfileStatus = state => {
    return state.profilePageData.status
};
const _getFullProfile = state => {
    return {
        profile: getProfile(state),
        status: getProfileStatus(state),
        myId: state.authData.id,
        // isAuth: state.authData.isAuth
    }
};
export const getFullProfile = createSelector(
    [_getFullProfile],
    profile => profile
);

export const _getPosts = state => {
    return state.profilePageData.posts
};
export const getPosts = createSelector(
    [_getPosts],
    posts => posts
);
