import React from 'react';
import { reduxForm } from "redux-form";

function Index({myId, profileId, uploadProfilePhoto}) {
    return Number(myId) === Number(profileId) && <UploadPhotoFormRedux onSubmit={ data => uploadProfilePhoto(data) }/>
}

const UploadPhotoForm = props => {
    const {onSubmit} = props;
    const onChange = e => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('image', e.target.files[0]);
        onSubmit(formData);
    };

    return (
        <form className={'Form'}>
            <span>
                Загрузить новое
            </span>
            <input
                type={'file'}
                name={'photo'}
                accept='.jpg, .png, .jpeg'
                onChange={onChange}
            />
        </form>
    )
};

const UploadPhotoFormRedux = reduxForm({
    form: 'upload-photo-form',
    // onSubmitSuccess: (result, dispatch) => dispatch(reset('upload-photo-form'))
})(UploadPhotoForm);

export default Index;
