import React, {useEffect, useState} from 'react';
import s from './Status.module.scss'
import Icon from '../../../common/Icon'

export default props => {
    // console.log('status');
    const {myId, profileId, status: propsStatus, updateProfileStatus} = props;
    const [status, setStatus] = useState(propsStatus);
    const [edit, setEdit] = useState(false);
    const [showPencil, setShowPencil] = useState(false);

    useEffect(()=> {
        setStatus(propsStatus);
    }, [propsStatus]);

    const canEdit = Number(myId) === Number(profileId);

    const mouseEnter = () => {
        if(canEdit) setShowPencil(true);
    };
    const mouseLeave = () => {
        if(canEdit) setShowPencil(false);
    };
    const onClick = () => {
        if(canEdit) setEdit(true);
    };
    const updateStatus = () => {
        if(canEdit) {
            setEdit(false);
            if(status !== propsStatus) updateProfileStatus(status);
        }
    };

    return (
        <div className={s.Status + (edit ? ' ' + s.active: '')}
             onClick={onClick}
             onMouseLeave={mouseLeave}
             onMouseEnter={mouseEnter}
        >
            {showPencil && <Icon icon={'pencil'} className={s.Pencil}/>}
            {edit
                ? <div className={s.Form}>
                    <input
                        className={s.Input}
                        type="text"
                        value={status}
                        onChange={e => setStatus(e.target.value)}
                        onBlur={updateStatus}
                        autoFocus={true}
                    />
                </div>
                : <div className={s.Text}>{status}</div>
            }
        </div>
    );
}
