import React, {useState} from 'react'
import s from './EditData.module.scss'
import { reset, Field, reduxForm} from "redux-form";
import { requiredField, minLength3, maxLength50 } from '../../../../utils/validators/validators';
import {Checkbox, Input, Textarea} from "../../../common/Forms/Controls";
import Preloader from '../../../common/Preloader'
import {connect} from "react-redux";
import Icon from "../../../common/Icon";

export default props => {
    const {myId, profile, updateProfileData} = props;

    if(Number(myId) !== Number(profile.userId)) return null;

    const [edit, setShowEdit] = useState(false);

    const showEdit = e => {
        e.preventDefault();
        setShowEdit(!edit)
    };

    const submitProfileData = data => {
        updateProfileData(data);
        setShowEdit(false);
    };

    if(!edit) return <span onClick={showEdit}><Icon icon={'pencil'} className={s.ShowIcon}/></span>;


    return (
        <div className={s.EditData}>
            <span onClick={showEdit}><Icon icon={'close'} className={s.CloseIcon}/></span>
            <UpdateProfileFormRedux profile={profile}  onSubmit={ data => submitProfileData(data)}/>
        </div>
    );
};

const UpdateProfileForm = props => {
    const {handleSubmit, invalid, profile} = props;

    if(!profile) return Preloader({loading: true});

    const {contacts} = profile;

    const Contacts = [];
    Object.keys(contacts).forEach( key =>
        Contacts.push(<Field
            key={key}
            component={Input}
            name={'contacts.'+key}
            validate={[minLength3]}
            label={key}
        />)
    );

    return (
        <form onSubmit={handleSubmit} className={'Form'}>
            <h3>Обо мне</h3>

            <Field
                label={'Имя'}
                component={Input}
                name={'fullName'}
                validate={[requiredField, minLength3, maxLength50]}
            />
            <Field
                component={Textarea}
                name={'aboutMe'}
                validate={[requiredField, minLength3]}
                label={'Текст обо мне'}
            />

            <div className={s.Collapse}>
                <h3 className={s.FormTitle}
                    onClick={e => e.currentTarget.classList.toggle(s.open)}
                >
                    Контакты
                    <Icon className={s.CollapseIcon} icon={'arrow-right'}/>
                </h3>
                <div className={s.Collapsable}>
                    {Contacts}
                </div>
            </div>

            <div className={s.Collapse}>
                <h3 className={s.FormTitle}
                    onClick={e => e.currentTarget.classList.toggle(s.open)}
                >
                    Работа
                    <Icon className={s.CollapseIcon} icon={'arrow-right'}/>
                </h3>
                <div className={s.Collapsable}>
                    <Field
                        type={'checkbox'}
                        component={Checkbox}
                        name={'lookingForAJob'}
                        label={'Ищу работу'}
                    />

                    <Field
                        component={Textarea}
                        name={'lookingForAJobDescription'}
                        validate={[requiredField, minLength3]}
                        label={'Текст о работе'}
                    />
                </div>
            </div>

            <div className={'Form_Row Form_Row__TextRight'}>
                <button type={'submit'} className={'Form_Button'} disabled={invalid}>Сохранить</button>
            </div>
        </form>
    )
};

let UpdateProfileFormRedux = reduxForm({
    form: 'update-profile-form',
    onSubmitSuccess: (result, dispatch) => dispatch(reset('update-profile-form'))
})(UpdateProfileForm);

UpdateProfileFormRedux = connect(
    state => ({
        initialValues: {...state.profilePageData.profile} // pull initial values from account reducer
    })              // bind account loading action creator
)(UpdateProfileFormRedux);
