import React from 'react';
import s from './Post.module.scss';

export default ({id, title, content, likesCount}) => (
    <div className={s.item} key={id}>
        <div className={s.picWrapper}>
            <img className={s.pic} src="https://i.pinimg.com/736x/62/8f/49/628f49798bba23a996bbb0fe8aad174e--avatar-james-cameron-avatar-fan-art.jpg" width="100" alt=""/>
            <span>{likesCount} likes</span>
        </div>

        <div className={s.ContentWrapper}>
            <div className={s.title}>{title}</div>
            <div className={s.text}>{content}</div>
        </div>
    </div>
);
