import {useEffect} from 'react';
import {compose} from "redux";
import {connect} from "react-redux";

import { getProfile, updateProfileStatus, uploadProfilePhoto, updateProfileData } from '../../../redux/profile/reducer';
import { getFullProfile } from '../../../redux/profile/selectors';

import ProfileInfo from "./index";
import Preloader from '../../common/Preloader'
import {withRouter} from "react-router-dom";

export default compose(
    withRouter,
    connect(
        state => ({
            ...getFullProfile(state),
            // myId: state.authData.id,
            // isAuth: state.authData.isAuth
        }),
        {
            getProfile,
            updateProfileStatus,
            uploadProfilePhoto,
            updateProfileData
        }
    )
)(props => {
    const {match: {params: {userId}}, myId, getProfile, history, ...other} = props;
    // console.log('ProfileInfoContainer');
    useEffect(
        () => {
            // console.log('ProfileInfoContainer useEffect');
            const id = Number(userId || myId);
            id ? getProfile(id): history.push('/login');
        },
        [userId, myId, getProfile, history]
    );
    return props.profile
        ? ProfileInfo({myId, ...other})
        : Preloader({loading: true});
})
